#__bs_notify__ { top:auto !important; bottom:-15px; right: 10px !important; border-radius: 15px !important; height:60px !important; }

.text-center {
  text-align: center;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

.hide {
  display: none;
}

.off {
  opacity: 0;
}

// popup opened
.oh {
  overflow: hidden !important;
  &.ios {
    position: fixed;
  }
}

@include breakpoint($only-desktop) {
  //shift for body and fixed elements when popup opened
  body.oh:not(.mac-os):not(.ios) {
    margin-right: 17px;

    .shiftMargin {
      margin-right: 17px !important;
    }
  }
}

h1.next-div-nopadding + *,
h2.next-div-nopadding + *,
h3.next-div-nopadding + * {
  padding-top: 0;
  margin-top: 0;
}

// popup opened END

.over {
  overflow: hidden;
}

.text-light {
  color: $text-gray-color;
}

.flR {
  float: right;
}

.text-lighter {
  color: $gray-text;
}

.clrfx {
  display: block;
  clear: both;
  height: 0;
  width: 100%;
}

.text-line {
  text-align: center !important;
}

.text-line:after {
  content: "";
  clear: both;
  display: block;
  margin: 2rem auto 0;
  width: 10rem;
  height: 1px;
  border-top: 2px solid $pink;
}

.l-container {
  margin: 0 2rem;
}

.brand-color {
  color: $pink;
}

[v-cloak] {
  display: none;
}

.transparent {
  opacity: 0;
  z-index: -1 !important;
}

.width-auto {
  min-width: auto !important;
  width: auto !important;
}

.inactive {
  background-color: $text-gray-color !important;
  border-color: $text-gray-color !important;
  cursor: default !important;
}

// editor
.pull-right {
  float: right;
  margin-left: 2rem;
  margin-bottom: 3rem;
}

.pull-left {
  float: left;
  margin-right: 3rem;
  margin-bottom: 2rem;
}

@include breakpoint($mobile) {
  .pull-left,
  .pull-right {
    float: none;
    width: 100%;
    margin: 0;
  }
}

.preloading {
  overflow: hidden;
  position: relative;
  // height: 20rem;
  @include transition(all 0.5s ease-out 0s);

  .preloadingContent {
    @include transition(all 0.35s ease-out 0s);
    opacity: 0;
  }

  &.finished {
    height: auto;
    .preloadingContent {
      opacity: 1;
    }
    .preloader {
      opacity: 0;
      z-index: -1;
    }
  }

  &:not(.finished) {
    .preloader {
      @include animation(preloading .5s linear infinite);
    }
  }
}

.preloader {
  @extend .absoluteFill;
  z-index: 10;
  margin: auto !important;
  width: 60px;
  height: 85px;
  margin: auto;
  background: url(data:image/jpeg;base64,) center 85px no-repeat;
  background-color: lighten($pink, 50%);
  mask-image: url(../img/icons/lobster_symbol.svg);
  mask-position:center center;
  mask-mode: alpha;
  mask-size: cover;
}


.bounce-alert {
  @include animation(bounce .5s ease-in);

  ::-webkit-input-placeholder { /* Chrome/Opera/Safari */
    color: red !important;
  }
  ::-moz-placeholder { /* Firefox 19+ */
    color: red !important;
  }
  :-ms-input-placeholder { /* IE 10+ */
    color: red !important;
  }
  :-moz-placeholder { /* Firefox 18- */
    color: red !important;
  }

  input[type='text'] {
    color: red !important;
  }
}

@include keyframes(bounce) {
	0%, 20%, 50%, 80%, 100% {
    @include transform(translateX(0));
  }
	40% {
    @include transform(translateX(-30px));
  }
	60% {
    @include transform(translateX(-15px));
  }
}

@include keyframes(preloading) {
	0% {
    background-position: center 85px;
  }
	100% {
    background-position: center 0;
  }
}
