// paddings & margins
.container-biggest-padding{
  padding: 0 10rem;

  @include breakpoint($desktop) {
  	padding: 0 7rem;
  }

  @include breakpoint($tablet) {
  	padding: 0 4rem;
  }

  @include breakpoint($mobile) {
  	padding: 0 2rem;
  }
}

.p10x8 {
  padding: 10rem 8rem;

  @include breakpoint($tablet) {
  	padding: 5rem 4rem;
  }
  @include breakpoint($desktop $lanscape) {
    padding: 5rem 4rem;
  }

  @include breakpoint($mobile) {
  	padding: 5rem 3rem;
  }
}

.bigTopPadding {
  padding-top: 7rem;
}

.container-big-padding{
  padding: 0 8rem;

  @include breakpoint($tablet) {
  	padding: 0 5rem;
  }

  @include breakpoint($mobile) {
  	padding: 0 3rem;
  }
}

.small-hpadding {
  padding:0 2rem;
}

.small-vpadding {
  padding:2rem 0;
}

.outline-margin {
  margin: 2rem;
}

.container {
  margin: 0 2rem;
}

.outerPadding5 {
  padding: 5rem;

  @include breakpoint($desktop) {
    padding: 3rem;
  }
}

// flex & flex options
.flex {
  display: flex;
  justify-content: space-around;
  flex-flow: row wrap;

  &.center {
    justify-content: center;
  }

  &.left {
    justify-content: flex-start;
  }

  &.space-between {
    justify-content: space-between;
  }
}

.mobileWide {
  @include breakpoint($mobile) {
    flex-wrap: wrap;
    flex-direction: column;

    > * {
      width: 100% !important;
      margin:  0 0 2rem 0 !important;
    }
  }
}

.halfWidth {
  width: 46%;
}

.followLink {
  font-size: $bigger-font-size;
  margin-top: .5rem;
  margin-left: 1rem;
  display: inline-block;
  font-size: 9px;
  color: #fff;

  &:hover {
    text-decoration: none;

    i {
      color: #fff;
    }
  }

  i {
    color: #d8d9d8;
    margin:0 0 0 1rem;
    font-size: 2rem;
  }
}

.absoluteFill {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 2;
  margin: 0 !important;
}

.absoluteCenter {
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  @include transform(translate3d(-50%,-50%,0));
  z-index: 10;
}

.stickyBottom {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10;
}

body .forShow {
  display: none;
  opacity: 0;
  z-index: 15;
  @include transition(all 0.25s ease-out);
}

.transparentWhite {
  background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABsAAAAQCAMAAADQzfSkAAAAA1BMVEX///+nxBvIAAAAAXRSTlPM0jRW/QAAAA1JREFUeAFjGNJgFAAAAcAAAeN1LDMAAAAASUVORK5CYII=);
}

.onHoverOverlay {
  &:before {
    content: '';
    @extend .absoluteCenter;
    background: #000;
    opacity: 0;
    z-index: 10;
    @include transition(all 0.15s ease-out );
  }

  img {
    position: relative;
    z-index: 2;
  }

  &:hover {
    &:before {
      opacity: 0.35;
    }
  }
}

.bottomBorder {
  background: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAfUAAAABCAYAAADHPDeqAAAAK0lEQVQ4T2Ps6en5X1JSwsgABaP80fAYTQ+j+WG0PICEwGh5OFoeDrXyEACBbva0+BAqwwAAAABJRU5ErkJggg==') 0 bottom repeat-x;
}

.popupOverlay {
  position: fixed;
  width: 100vw;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  // background: #000;
  opacity: 0;
  visibility: hidden;
  @include transition(all 0.45s ease-out 0s);
  z-index: 15;
}

.fixedToTop {
  position: fixed;
  // top: 0;
  left: 0;
  right: 0;
  z-index: 20;
}

.embededVideo {
  height: 0;
  padding-bottom: 56.25%; // 16/9
  position: relative;

  iframe {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }
}

.bgCover {
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

.darkBg {
  background: $dark-text;
  color: $text-gray-color;

  h1,h2 {
    color: #fff;
  }
}

.showOnM {
  display: none !important;
}


@include breakpoint($mobile) {
  .hideOnM {
    display: none !important;
  }
  .showOnM {
    display: block !important;
  }
}

@mixin bordered-btn($color) {
	border-color: $color;
	color: $color;
  background: none;

	&:hover {
		background-color: rgba(158, 158, 158, 0.2);
	}
}

@mixin positioned-label($pad) {
  position: absolute;
  left: $pad;
  top: $pad;
}

@mixin stickyBottom {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10;
}

@mixin font-size($font-size) {
  font-size: $font-size;

  @include breakpoint($mobile) {
    font-size: $font-size * 1.303;
  }
}


.imgOverlay {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.4);
}
