body.padding-top {
  @include breakpoint($mobile) {
    padding-top: 9.3rem;
  }
}
.content {
  padding: 0 1rem;

}
.footer__line {
  display: none !important;
}

.topSection{
  @extend .flex;
  justify-content: space-between;
  // flex-wrap: nowrap;
  z-index: 30;



  &:before {
    display: none;
  }

  &.mobile {
    display: none;
    padding:2rem 3rem;
  }

  @include breakpoint($mobile) {
    
    &.desktop {
      overflow: hidden;
    }

    &.mobile {
      display: block;
      display: flex;
      justify-content: space-between;
      align-items: center;

      > * {
        margin: 0 !important;
      }
    }
  }

  .switch-categories &,
  .mobile-search-show &{
    box-shadow: none;
    border-bottom: 1px solid $border-color;
  }

  &__logo {
    float: none;
    flex-shrink: 0;
    margin-right: 20px;

    .show-switcher & {
      margin-right: 50px;
    }
  }

  .icon-category {
    color: $input-border;

    .switch-categories & {
      color: $pink;
    }
  }

  &__catSwitcher {
    font-size: 1.8rem;
    color: $pink;
    position: absolute;
    top: 0;
    left: -75px;
    bottom: 0;
    cursor: pointer;
    z-index: 10;
    display: none !important;
    @include transition(all 0.15s ease-out 0s);

    &:hover {
      @include transform(rotate(90deg));
    }

    &:active {
      @include transform(rotate(90deg) translateX(5px));
    }

    .show-switcher &{
      display: block !important;
    }
  }

  &__search {
    width: 200px;
    height: 30px;
    position: relative;
    overflow: hidden;

    .show-switcher & {
      width: 250px;
    }

    .slideContainer {
      position: absolute;
      right: 0;
      @include transform(translate3d(100%,0,0));
      @include transition(all 0.35s ease-out 0s);
      white-space: nowrap;

      &.on {
        @include transform(translate3d(0,0,0));
        .icon-magnifier {
          color: $pink;
        }
      }

      > * {
        display: inline-block;
      }

      .icon-magnifier {
        font-size: 2rem;
        cursor: pointer;
        position: absolute;
        left: -25px;
        color: $input-border;
      }

      input[type='text'] {
        outline: none;
        padding-left: 3px;
        @include transition(all 0.25s ease-out 0s);

      }

      input[type='submit'] {
        display: none;
      }
    }

    input {
      border: none;
      border-bottom: 1px solid $border-color;
      font-family: $default-font;
      font-size: 1.4rem;
    }
  }

}
.categories {
  text-transform: uppercase;
  white-space: nowrap;
  text-align: center;


  .show-switcher &:not(.tablet) {
    display: none;
  }

  &.tablet {
    @extend .fixedToTop;
    box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.2);
    @include transition(height 0.25s ease-out 0s);
    font-size: $smaller-font-size;
    background: #fff;
    height: 0;
    overflow: hidden;

    @include breakpoint($mobile) {
      white-space: normal;
      text-align: left;
      padding: 0 3rem;
      font-size: $smaller-font-size-fixed;

      li {
        width: 48%;
        padding: 1.3rem 0;
        margin: 0;
      }
    }

    ul{
      padding: 1.5rem 0;
    }

  }

  &__item {
    margin: 0 1rem;
    display: inline-block;

    &.item--active {
      a {
        color: $pink !important;
      }
    }
  }
}

.mobileSearchSwitcher {
  color: $input-border;
  font-size: 3.2rem;
}

.searchMobile {
  @extend .fixedToTop;
  height: 0;
  overflow: hidden;
  box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.2);
  @include transition(height 0.15s ease-out 0s);

  .mobile-search-show & {
    height: 50px;
  }

  input[type='text'] {
    color: #000;
    outline: none;
    width: 100%;
    padding:0 3rem;
    height: 50px;
    margin: 0;
    border: none;
    font-size: 16px;
  }

  input[type='submit'] {
    display: none;
    @include appearance(none);
    outline: none;
  }

  @include breakpoint($only-desktop) {
    display: none;
  }
}
