.heroSection {
  text-align: center;
  position: relative;
  min-height: 20rem;
  @extend .flex;
  justify-content: center;
  align-items: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;

  &__text {
    color: #fff;
    font-size: $bigger-font-size;
    width: 100%;
    font-weight: 300;
    padding:0 4rem;
    text-shadow: 1px 1px 0 #000;

    h1 {
      font-size: 4.6rem;
      margin-bottom: 2.5rem;
      font-weight: normal;
    }
  }

}

.articlesList {
  max-width: 750px;
  margin: auto;
  margin-top: 5rem;

  @include breakpoint($mobile) {
    margin-top: 3rem;
  }

  &__item:not(.articleDetail) {
    padding-bottom: 4rem;
    margin-bottom: 5rem;
    border-bottom: 1px solid $border-color;
  }
}

.author {
  font-size: $smaller-font-size;
  color: #bdbdbd;
  @extend .flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 1.3rem;


  a {
    color: $brand-green;
  }

  &__ava {
    margin-right: 1rem;
    width: 35px;
    height: 35px;
    border-radius: 100%;
  }

  &__info {
    line-height: 1.8rem;
    overflow: hidden;

    @include breakpoint($mobile) {
      font-size: 13px;
      line-height: 18px;
    }
  }
}

.articlePreview {
  h2 {
    font-weight: bold;
    margin-bottom: 1rem;

    a {
      color: $main-color;

      &:hover {
        color: $brand-green;
        text-decoration: none;
      }
    }
  }

  &__img {
    width: 100%;
    height: auto;
    margin-bottom: 2rem;
  }

  &__moreLink {
    display: inline-block;
    margin-top: 1.5rem;
    font-size: $smaller-font-size-fixed;
    color: $brand-green;
  }

  &__bottom {
    // @include transition(margin 0.25s ease-out 0s);
    @include transform(translate3d(0,0,0));
    padding: .7rem 0 2rem 0 !important;

    z-index: 10;
    position: relative;
    background: #fff;

    .in {
      @extend .flex;
      justify-content: space-between;
      margin-top: 1.3rem;
      max-width: 75rem;
      margin: auto;
    }

    &.sticky {
      position: fixed;
      left: 0;
      right: 0;
      bottom: 0;
      margin: 0 !important;
      border-top: 1px solid $border-color;

      .shareBlock {
        // margin: 0;
      }
    }
  }
}

.tagsBlock {
  @extend .flex;
  justify-content: flex-start;
  align-items: center;
  font-size: $smaller-font-size;
  margin-top: 1rem;

  @include breakpoint($mobile) {
    font-size: 14px;
  }

  &__item {
    margin-left: 1.9rem;
    color: $text-gray-color;

    &:hover {
      text-decoration: none;
      color: $dark-text
    }
  }

  &__icon {
    font-size: 1.5rem;
    color: $inactive-label;
    margin-right: 0;
    @include breakpoint($mobile) {
      font-size: 2.5rem;
    }
  }
}


.shareBlock {
  @extend .flex;
  justify-content: flex-start;
  align-items: center;
  margin-top: 1rem;
  padding-left: 2px;

  [class^="icon-"], [class*=" icon-"] {
    color: #bdbdbd;
    font-family: ProximaNova;
  }



  &__item {
    margin-right: 1.5rem;
    text-decoration: none !important;
    font-size: 1.8rem;
    line-height: 2rem;
    @include transition(color 0.25s ease-out 0s);
    position: relative;
    cursor: pointer;

    &[class^="-filled"], &[class*="-filled"] {
      &:before {
        color: $pink;
      }
      span {
        margin-left: 5px;
      }
    }

    .h {
      position: absolute;
      left: 0;
      top: 0;
      font-size: 1.8rem;
      color: $pink;
      @include transition(left 0.9s linear, top 0.9s linear, opacity 0.8s ease-out 10ms);

      & + .h { @include transition-delay(0.15s);  }
      & + .h + .h { @include transition-delay(0.05s); }
      & + .h + .h + .h{ @include transition-delay(0.1s); }
      & + .h + .h + .h + .h { @include transition-delay(0.08s); }
      & + .h + .h + .h + .h + .h{
        @include transition-delay(0.2s);
        @include transition(left 1.9s linear, top 1.9s linear, opacity 1.8s ease-out 10ms);

        &:before {
          content: "\e909";
          font-size: 2rem;
        }
      }
    }

    &.animate {
      .h {
        opacity: 0;
      }
    }

    @include breakpoint($mobile) {
      font-size: 20px;
      margin:0 3rem 0 0;
      cursor: default;
    }

    &:before {
      display: inline-block;
      vertical-align: middle;
    }

    &:hover {
      &:before {
        color:$pink;
      }
    }

    span {
      display: inline-block;
      vertical-align: middle;
      font-size: $smaller-font-size;
      font-family: $default-font;
      line-height: 1;
      margin-left: 4px;

      @include breakpoint($mobile) {
        font-size: $smaller-font-size-fixed;
      }
    }

    &.show--submenu {
      @include perspective(200px);
    }
    @include breakpoint($tablet) {
      &.show--submenu.on {
        .shareBlock__submenu {
          opacity: 1;
          z-index: 10;
          visibility: visible;
          @include transform(rotateX(0deg));
        }
      }
    }
    @include breakpoint($only-desktop) {
      &.show--submenu:hover {
        .shareBlock__submenu {
          opacity: 1;
          z-index: 10;
          visibility: visible;
          @include transform(rotateX(0deg));
        }
      }
    }
  }

  &__submenu {
    position: absolute;
    padding-bottom: 20px;
    left: 0;
    bottom: 10px;
    line-height: 35px;
    white-space: nowrap;
    font-size: 0;
    @include transition(all 0.15s ease-out);
    @include transform(rotateX(100deg));
    @include transform-origin(center bottom);
    z-index: -1;
    opacity: 0;
    visibility: hidden;

    &:before {
      content: '';
      display: block;
      position: absolute;
      bottom: 15px;
      left: 8px;
      @include triangle(12px 6px, $pink, down);
    }

    li {
      display: inline-block;
      vertical-align: middle;

      &:first-child a{
        border-radius: 2px 0 0 2px;
      }
      &:last-child a{
        border-radius: 0 2px 2px 0;
      }
    }

    a {
      display: block;
      vertical-align: top;
      width: 30px;
      text-align: center;
      height: 34px;
      line-height: 35px;
      font-size: 1.8rem;
      margin: 0;
      background: $pink;
      cursor: pointer;
      text-decoration: none !important;
      outline: none;

      &:before {
        color:#fff;
      }
      @include breakpoint($only-desktop) {
        &:hover {
          background-color: lighten($pink, 10%);
        }
      }
    }
  }
}

.articleDetail {
  @extend .articlesList;

  .articlePreview__bottom .in{
    @include breakpoint($mobile) {
      flex-direction: column;

      > *  {
        justify-content: center;
      }
    }
  }
  @include breakpoint($mobile) {
    .shareBlock {
      margin-bottom: 2rem;
    }

    .shareBlock__item {
      margin: 0 1.5rem;
    }
  }

  a {
    color: $brand-green;
  }

  &__img {
    img{
      max-width: 100%;
      height: auto !important;
    }
  }

  &__text {
    font-size: 1.8rem;
    line-height: 2.5rem;
    color: $dark-text;
    font-weight: 300;

    img{
      max-width: 100%;
      height: auto !important;
    }

    @include breakpoint($mobile) {
      font-size: 14px;
    }

    h1 {
      margin: 3rem 0;

      @include breakpoint($mobile) {
        margin: 2rem 0;
        font-size: 22px;
      }
    }

    h2 {
      margin: 2rem 0 1rem 0;
      font-weight: normal;
    }

    h3 {
      margin: 2rem 0 1rem 0;
      font-size: 2.5rem;
      font-weight: normal;
    }

    em {
      color: #e52591;
      font-weight: 300;
    }

    ol {
      list-style-type: decimal;
      margin: 1rem 0;
      padding-left: 2rem;

      li {
        padding-bottom: .8rem;
      }
    }

    ul {
      margin: 1rem 0;
      padding: 0;

      li {
        padding-bottom: .8rem;

        &:before {
            content: "•";
            display: inline-block;
            margin-right: 1.5rem;
            font-size: 3rem;
            color: $pink;
        }
      }
    }
  }
  .articlePreview__bottom {
    margin-top: 2.3rem;
    margin-bottom: 2rem;

  }
}


.disqusBlock {
  margin-top: 5rem;
}

.authorsSection {
  text-align: center;
  padding: 4.5rem 0;
  border-bottom: 1px solid $border-color;
  color: $dark-text;
  font-size: 1.8rem;
  font-weight: 300;
  border-top: 1px solid $border-color;

  &__img {
    padding-bottom: 2rem;

    img {
      display: block;
      margin: auto;
      width: 7.5rem;
      height: 7.5rem;
      border-radius: 100%;
    }
  }
  &__name {
    font-size: 2.5rem;
    font-weight: bold;
    padding-bottom: .5rem;
  }

  &__email {
    padding-bottom: .5rem;
    font-style: italic;
  }

  &__description {
    font-style: italic;
  }
}


.simplePagination {
  @extend .flex;
  padding: 2.5rem 0;
  justify-content: space-between;
  align-items: center;
  font-size: 1.6rem;
  font-weight: 300;
  line-height: 2.3rem;
  flex-wrap: nowrap;
  margin-top: -5rem;

  &__list{
    white-space: nowrap;
    font-size: 0;

    li {
      font-size: $smaller-font-size-fixed;
      display: inline-block;
      margin: 0 .8rem;

      .active {
        color: $pink;
      }

      a {
        color: #000;
        display: block;
        padding: .5rem;
      }
    }
  }

  .simplePagination__text {
    text-align: center;
  }

  .simplePagination__left {
    @include transform(rotate(180deg));
  }

  .simplePagination__left,
  .simplePagination__right {
    margin: 0;
    color: $pink;
    text-decoration: none !important;
    font-size: 3rem;

    &.inactive {
      visibility: hidden;
    }
  }
}

.searchTitle {
  max-width: 750px;
  margin: auto;
  @extend .h2;
  font-weight: normal;
  padding-top: 4.5rem;
  padding-bottom: 1.5rem;
  border-bottom: 1px solid $border-color;

  em {
    color: $pink;
    &:before {
      content: "\201c";
    }
    &:after {
      content: "\201d";
    }
  }
}

.socialMenu {
  text-align: center;
  z-index: 2;
  display: inline-block;
  margin-left: 2rem;

  @include breakpoint ($mobile) {
    display: flex;
    justify-content: space-between;
    flex-grow: 10;
    white-space: nowrap;
  }

  &__item {
    display: inline-block;
    margin: 0 1rem;
    text-align: center;
    font-size: 0.8em;
    position: relative;
    font-size: 0;
    text-align: center;
    box-sizing: content-box;
    overflow: hidden;
    background: $gray;
    border-radius: 100%;
    border: none;
    width: 32px;
    height: 32px;
    line-height: 32px;

    @include breakpoint ($mobile) {
      width: 4.2rem;
      height: 4.2rem;
      margin: 0 0.1rem;
      line-height: 4.5rem;
    }

    [class^="icon-"], [class*=" icon-"] {
      margin: 0;
      font-size: 2.1rem;
      font-size: $h3-size;
      color: #fff;
    }

    a {
      display: block;

      @include breakpoint ($only-desktop) {
        &:hover {
          background: $pink;

          i {
            color: #fff;
          }
        }
      }
    }
  }
}
