.blocks {

  &__row {
    overflow: hidden;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 2rem 0;
    margin-bottom: 0;

    @include breakpoint($mobile) {
      flex-direction: column;
    }

    &:nth-child(2n) {
      flex-direction: row-reverse;
    }

    @include breakpoint($mobile) {
      flex-direction: column !important;
      margin: 0;
    }
  }

  &__btn {
    @extend .bordered;
    @extend .big--btn;
  }

  &__img {
    width: calc(100%/2 - 1rem);
    background-size: cover;
    background-position: center center;
    position: relative;

    @include breakpoint($mobile) {
      width: 100%;
      height: 50rem;
      margin-bottom: 2rem;
    }
  }

  &__in {
    width: 100%;
  }

  &__text {
    width: calc(100%/2 - 1rem);
    @extend .p10x8;
    border: 1px solid $gray-border;
    font-size: $bigger-font-size;
    color: $text-gray-color;
    min-height: 62.5rem;
    @extend .flex;
    align-items: center;

    @include breakpoint($desktop) {
      min-height: 50%;
    }
    @include breakpoint($xdesktop) {
      min-height: 53rem;
    }

    @include breakpoint($tablet) {
      min-height:inherit;
    }

    @include breakpoint($mobile) {
      width: 100%;
    }

    .btn {
      margin-bottom: 1.5rem;
      @include calc(margin-right, '1.5rem - 2px');
    }
  }

  &.reverted {
    .blocks__text {
      order: -1;

      @include breakpoint($mobile) {
        order: 1;
      }
    }
  }

  &__textBody {
    margin-top: 3rem;
  }

  &__header {
    color: $pink;
    font-weight: normal;
    font-size: $h1-size-fixed;
  }

  &__subheader {
    font-size: 2.5rem;
    font-weight: 300;
  }
}

.quote {
  margin-top: 6.5rem;

  &__img {
    float: right;
    width: 7.5rem;
    height: 7.5rem;
    border-radius: 100%;
  }

  &__text {
    @include calc(width, '100% - 10rem');
    font-size: $default-font-size;
    font-style: italic;
    color: #000;
    margin-bottom: 1.5rem;
  }

  &__author {
    @include calc(width, '100% - 10rem');
    font-size: $smaller-font-size;

    strong {
      font-weight: bold;
      color: $gray-text;

      // &:after {
      //   content: ',';
      // }
    }
  }
}
.featuresSection {
  padding: 7rem 0 0 0;

  h2 {
    margin-bottom: 6rem;
    font-size: 2.8rem;
  }
}

.features {
  justify-content: center;
  font-size: $default-font-size;
  max-width: 170rem;
  margin: auto;
  @extend .threeCol;


  &.fc {
    @extend .fourCol;
    @include breakpoint($desktop) {
      .cItem {
        &:nth-child(1n),
        &:nth-child(3n) {
          margin-right: 20px;
        }
        &:nth-child(2n) {
          margin-right: 0;
        }
        @include calc(width, '(99.99% * 1/2) - (20px - 20px * 1/2)');
      }
    }
  }

  &__icon {
    max-width: 50px;
    margin-right: 2rem;
    float: left;
  }

  &__text {
    overflow: hidden;
    margin-bottom: 1rem;

    h3 {
      font-size: $bigger-font-size;
      margin-bottom: 1rem;
    }
  }
}

.specialistsFeature{
  @extend .small-hpadding;
  text-align: center;
  background-color: $gray-bg;
  padding-top: 7.5rem;
  padding-bottom: 10rem;

  h2 {
    margin-bottom: 4.5rem;
  }

  p {
    display: block;
    max-width: 88rem;
    margin-left: auto;
    margin-right: auto;
  }
}

.coursesContainer:not(.LPpage) .coursesSection {
  @extend .fourCol;
}

.coursesSection {
  justify-content: center;
  margin-bottom: -2rem;

  > div {
    margin-bottom: 2rem;
  }

  &__header{
    width: 100%;
    margin-bottom: 4.5rem;
  }

  &__item {

    @include breakpoint ($tablet) {
      margin-bottom: 3rem;
    }

    img {
      display: block;
      max-width: 100%;
      height: auto;
    }
  }

  &__img {
    @extend .onHoverOverlay;
    position: relative;
    display: block;
  }

  &__label {
    position: absolute;
    margin: 1rem;
    font-size: $smaller-font-size;
    padding:0 1.5rem;
    line-height: 3rem;
    background: $pink;
    color: #fff;
    border-radius: 3px;
    cursor: default;
    z-index: 3;
    text-transform: uppercase;

    &.gray {
      background-color: $gray-bg;
      color: $dark-text;
    }
  }

  &__play {
    @extend .absoluteCenter;
    @include transform(none);
    width: 5.9rem;
    height: 5.9rem;
    margin:-3rem 0 0 -3rem;
    line-height: 5.9rem;
    color: #fff;
    font-size: 6.5rem;
    @extend .icon-font;
    @extend .icon-play-circle;
  }

  &__title {
    font-size: $bigger-font-size;
    color: $brand-green;
    margin-bottom: 1.5rem;

    a {
      color: $brand-green;
      text-decoration: none;
    }
  }

  &__text {
    border: 1px solid $gray-border;
    padding: 2rem;

    @include breakpoint ($not-mobile) {
      padding-bottom: 5rem;
      height: 26rem;
      position: relative;
    }

    p {
      margin-bottom: 1rem;
      display: block;
      overflow: hidden;
      text-overflow: ellipsis;

      @include breakpoint ($not-mobile) {
        max-height: 13.5rem;
      }

      @include breakpoint($tablet) {
        height: auto;
      }
    }
  }

  &__moreLink{
    font-size: $smaller-font-size;
    text-decoration: none;
    color: $brand-green;

    @include breakpoint ($not-mobile) {
      left: 0;
      bottom: 0;
      right: 0;
      padding:1rem 2rem 2rem;
      background: #fff;
      position: absolute;
    }
  }

}

//learning page difference
.LPpage .coursesSection {
  @extend .twoCol;

  .cItem {
    @extend .flex;
    flex-wrap: nowrap;
    overflow: hidden;
  }

  &__img {
    width: 51%;
    overflow: hidden;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
  }

  &__text {
    width: 49%;
    height: 20rem;
    padding: 3rem;
    position: relative;


    &:after {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      bottom: -1px;
      right: 0;
      height: 2rem;
      background: #fff;
      z-index: 2;
      border-bottom: 1px solid $gray-border;
    }

    @include breakpoint($xdesktop)  {
      padding: 2rem;

      &:after {
        height: 1.5rem;
      }
    }

    p {
      overflow: hidden;
      @include breakpoint($only-desktop)  {
        max-height: 9rem;
      }
    }
  }

  &__title {
    margin-bottom: .5rem;
  }

  &__time {
    font-size: $smaller-font-size;
    margin-bottom: 1.5rem;
    color: $text-gray-color;

    span {
      display: inline-block;
      margin-left: 1rem;
    }
  }


  @include breakpoint ($desktop) {
    .cItem {
      flex-wrap: wrap;
    }

    &__img {
      width: 100%;
      height: 20rem;
    }

    &__text {
      width: 100%;
    }
  }
  @include breakpoint ($mobile) {
    &__img {
      height: 20rem;
    }
    &__text {
      height: auto;
      max-height: 20.5rem;
    }
  }
}


