.featuresList{
  width: 90%;

  span {
    overflow: hidden;
    display: block;
  }

  &__item {
    margin-bottom: 1.5rem;
    overflow: hidden;

    @include breakpoint($mobile) {
      margin-bottom: 3rem;
    }

    &:before {
      content: '';
      display: block;
      background: url(../img/icons/lesson-icon.svg) 0 0 no-repeat;
      height: 4.5rem;
      width: 5.5rem;
      float: left;
    }

    &.book-icon:before {
      background: url(../img/icons/assessments-icon.svg) 0 0 no-repeat;
    }

    &.languages-icon:before {
      background: url(../img/icons/languages-icon.svg) 0 0 no-repeat;
    }

  }

  &__text {
    overflow: hidden;
  }

  &__header {
    display: block;
    text-transform: uppercase;
    font-size: 11px;
    color: $gray-text;
    margin-bottom: 5px;
  }

}


.socialSection {
  text-align: center;
  padding: 8rem 0 6rem 0;

  h3 {
    line-height: 4rem;
    margin-bottom: 2rem;
    font-weight: normal;
  }
}

.socialMenu {
  text-align: center;
  z-index: 2;
  display: inline-block;

  @include breakpoint ($mobile) {
    padding: 0 1rem;
  }

  &__item {
    display: inline-block;
    margin: 0 0.5rem;
    border: 1px solid $pink;
    width: 5.5rem;
    height: 5.5rem;
    line-height: 5.5rem;
    text-align: center;
    font-size: 0.8em;
    position: relative;
    font-size: 0;
    text-align: center;
    box-sizing: content-box;
    overflow: hidden;

    @include breakpoint ($mobile) {
      width: 4.2rem;
      height: 4.2rem;
      margin: 0 0.1rem;
      line-height: 4.5rem;
    }

    [class^="icon-"], [class*=" icon-"] {
      margin: 0;
      font-size: 2.1rem;
      color: $pink;
    }

    a {
      display: block;

      @include breakpoint ($only-desktop) {
        &:hover {
          background: $pink;

          i {
            color: #fff;
          }
        }
      }
    }
  }
}

.learningTop {
  min-height: 37rem;
  padding: 3rem;
  @extend .flex;
  justify-content: center;
  align-items: center;

  &__headline {
    font-size: 4.8rem;
    font-weight: normal;
    margin-bottom: 2rem;
  }

  &__text {
    padding-bottom: 5rem;
  }

  &__btn {
    min-width: 18rem;
  }

  .in {
    max-width: 95rem;
    color: #fff;
    text-align: center;
    font-size: $bigger-font-size;
  }
}

.reviewSection {
  margin-top: 9rem;
  background: $gray-bg;
  padding: 8rem 0;

  @include breakpoint ($mobile) {
    padding: 5rem 0;
  }

  &__header{
    margin-bottom: 6rem;
  }

  &__list{
    overflow: hidden;
    padding:0 13rem;
    max-width: 170rem;
    margin: auto;

    .slick-list {
      margin-right: -6rem;
    }

    @include breakpoint ($desktop) {
      padding:0 5rem;

      .slick-list {
        margin-right: -3rem;
      }
    }
    @include breakpoint ($mobile) {
      .slick-list {
        margin-right: 0;
      }
    }
  }

  &__item{
    float: left;
    @include breakpoint ($mobile) {
      text-align: center;
    }
  }

  &__text{
    overflow: hidden;
    padding-right: 6rem;

    @include breakpoint ($desktop) {
      padding-right: 3rem;
    }
    @include breakpoint ($mobile) {
      padding-right: 0;
    }


    p {
      font-style: italic;
      overflow: hidden;
      margin-bottom: 1rem;
      font-weight: 300;
      @include breakpoint ($only-desktop) {
        max-height: 9rem;
      }
    }
  }

  &__name{
    font-size: $smaller-font-size;
    color: $gray-text;
  }

  &__img{
    float: left;

    @include size(7.5rem);
    border-radius: 100%;
    margin-right: 1.5rem;

    @include breakpoint ($mobile) {
      float: none;
      display: inline-block !important;
      margin-right: 0;
      margin-bottom: 1rem;
    }
  }

  .slick-arrow {
  	font-size: 0 !important;
  	background: none;
  	border: none;
  	padding: 0 !important;
  	margin: 0 !important;
  	@extend .icon-next;
  	@extend .icon-font;
  	position: absolute;
  	top: 15px;
  	right: 50px;
  	color: $pink;
  	@include appearance(none);
  	outline: none;
  	cursor: pointer;

  	&.slick-prev {
  		right: auto;
  		left: 50px;
  		@include transform(rotate(180deg));
  	}

  	@include breakpoint ($desktop) {
  		right: 5px;

  		&.slick-prev {
  			left: 5px;
  		}
  	}

  	&:before {
  		font-size: 5rem;
  	}
  }

}
