//columned blocks mixin
.twoCol,
.threeCol,
.fourCol,
.sixCol {
  @extend .flex;
  justify-content: flex-start;
  align-items: flex-start;
  justify-content: flex-start;

  img {
    flex-shrink: 0;
  }

  .cItem {
    margin-bottom: 2rem;
    margin-right: 2rem;
  }

  @include breakpoint($mobile) {
    flex-direction: column;
    .cItem {
      min-width: 100%;
      margin-right: 0 !important;
      margin-left: 0 !important;
    }
  }

  //because of isotope can't recognize flex correctly
  &.filterresults {
    .cItem {
      margin-right: 0 !important;
    }
  }
}
// ********************************************TWO columns****************************************************
.twoCol {
  .cItem {
    @include calc(width, '99.99% * 1/2 - (20px - 20px * 1/2)');
    &:nth-child(2n) { margin-right: 0;}
  }
}

// ********************************************THREE columns****************************************************
.threeCol {
  .cItem {
    @include calc(width, '99.99% * 1/3 - (20px - 20px * 1/3)');
    &:nth-child(3n) { margin-right: 0;}
  }

  @include breakpoint($tablet) {
    justify-content: space-between !important;
    .cItem {
      @include calc(width, '(99.99% * 1/2) - (20px * 1/2)');
      margin-right: 0;
    }
  }
}

// ********************************************FOUR columns****************************************************
.fourCol {
  .cItem {
    @include calc(width, '99.99% * 1/4 - (20px - 20px * 1/4)');
    &:last-child {margin-right: 0 !important;}
    &:nth-child(3n) {margin-right: 20px; }
    &:nth-child(4n) {margin-right: 0; }
  }

  @include breakpoint($desktop) {
    .cItem {
      &:nth-child(4n)  {margin-right: 20px; }
      &:nth-child(3n)  {margin-right: 0; }
      @include calc(width, '(99.99% * 1/3) - (20px - 20px * 1/3)');
    }
  }

  @include breakpoint($tablet) {
    .cItem {
      &:nth-child(1n),
      &:nth-child(3n)  { margin-right: 20px; }
      &:nth-child(2n)  { margin-right: 0; }
      @include calc(width, '(99.99% * 1/2) - (20px - 20px * 1/2)');
    }
  }
  @include breakpoint($tablet) {
    .cItem             { margin-right: 0;}
  }
}

.fourTwo { //this class neede if you want to exclude threecol behavior from four col in responcive
  @include breakpoint($desktop) {
    .cItem {
      @include calc(width, '99.99% * 1/2 - (20px - 20px * 1/2)');
      &:nth-child(1n),
      &:nth-child(3n) { margin-right: 20px; }
      &:nth-child(2n) { margin-right: 0;}
    }
  }
}
// ********************************************SIX columns****************************************************

.sixCol {
  .cItem {
    @include calc(width, '99.99% * 1/6 - (2rem - 2rem * 1/6)');
    &:nth-child(6n)   { margin-right: 0;}
  }

  @include breakpoint($desktop) {
    .cItem {
      @include calc(width, '99.99% * 1/4 - (2rem - 2rem * 1/4)');
      &:nth-child(1n) { margin-right: 2rem; }
      &:nth-child(4n) { margin-right: 0; }
    }
  }

  @include breakpoint($mobile) {
    flex-direction: row !important;
    .cItem {
      min-width: inherit;
      @include calc(width, '99.99% * 1/2 - (2rem - 2rem * 1/2)');

      &:nth-child(1n) { margin-right: 2rem !important;}
      &:nth-child(2n) { margin-right: 0 !important;}
    }
  }
}

