.flexThirdColumn {
  @include calc(width, '99.99% * 1/3 - (20px - 20px * 1/3)');
  flex-shrink: 0;
  margin-left: 20px;

  @include breakpoint($tablet) { @include calc(width, '50% - (20px/2)'); }
}

.filtersBlock {
  width: 80%;
  margin: auto;
  @extend .flex;
  margin-top: 6rem;
  margin-bottom: 6rem;
  justify-content: center;

  &__item {
    position: relative;

    @include breakpoint($mobile) {
      margin-bottom: 5rem !important;
    }
  }

  &.twoCol {
    width: 100%;
    max-width: 600px;

    @include breakpoint($tablet) {
      width: 70%;
    }

    @include breakpoint($mobile) {
      width: 90%;

      .filtersBlock__item {
        margin-bottom: 5rem;
      }
    }
  }

  .select2 {
    width: 100%;
    max-width: 100%; //it need when you resize parent select2 container
  }

}

.select2-results__option {
  @extend .flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: nowrap;

  img {
    max-width: 20px;
    max-height: 25px;
    width: auto;
    height: auto;
    margin-right: 20px;
  }

  span:first-child {
    width: 35px;
  }

  .icon {
    @extend .flex;
    align-items: center;
  }
}


.iconBlock {
  @extend .flex;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: flex-start;
  margin-bottom: 1.5rem;

  img {
    max-width: 20%;
    margin-right: 2rem;
  }

  p {
    margin: 0;
  }
}
