.container {
  background-size: cover;
}

.header {
  @include breakpoint($tablet) {
    overflow: hidden;
  }
}

// body:not(.innerPage) .header,
.videoBgWrapper,
.backgroundGraphic {
  height: 100vh;

  @include breakpoint($iphone6PlusDown $lanscape) {
    min-height: 450px;
  }

  &.low{
    height: 46rem;
  }

  *::-webkit-media-controls-panel {
    display: none !important;
    -webkit-appearance: none;
  }

  *::--webkit-media-controls-play-button {
    display: none !important;
    -webkit-appearance: none;
  }

  *::-webkit-media-controls-start-playback-button {
    display: none !important;
    -webkit-appearance: none;
  }
}

.videoBgWrapper {
  position: relative;
}

.sidebar {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  width: 304px;
  z-index: 100;
  background: #fff;
  padding: 2rem 4rem;
  font-size: $smaller-font-size-fixed;
  @include transition(all 0.3s ease-out 0s);
  line-height: 40px;
  @include transform(translate3d(-100%,0,0));
  z-index: 101;


  &__overlay {
    @extend .popupOverlay;
  }

  .sidebar--show & {
    @include transform(translate3d(0,0,0));

    &__overlay {
      visibility: visible;
      opacity: .48;
      background: #000;
    }
  }

  .icon-close {
    position: absolute;
    right: 2rem;
    top: 2rem;
    margin: 0;
    color: #000;
    opacity: .45;
    font-size: 28px;
    cursor: pointer;
  }

  a:not(.btn) {
    color: $dark-text;
  }

  &__menu {
    clear: both;
    font-size: $smaller-font-size-fixed;
    text-transform: uppercase;

    li {
      position: relative;

      a {
        text-decoration: none;
      }
    }

  }
  .topSection__login {
    width: 100%;
    font-weight: bold;
    margin-top: 4rem;
  }
}

.topSection{
  position: fixed;
  left: 0;
  right: 0;
  z-index: 20;
  font-size: $smaller-font-size;
  top: 0;
  padding: 2rem 7rem 2rem 7rem;;
  line-height: 4rem;
  background-color: transparent;
  text-align: right;
  max-height: 40px;
  box-sizing: content-box;
  font-size: $smaller-font-size-fixed;
  @include transition(background 0.3s ease-out 0s);

  .icon-navicon {
    font-size: 28px;
    color: #fff;
  }

  .mainMenu {
    display: inline-block;
    margin: 0;
    padding: 0;

    li {
      display: inline-block;
      margin-left: 2rem;
      text-transform: uppercase;
      position: relative;
      z-index: 200;

      &.forShow {
        display: none;
      }

      > a {
        color: #fff;
        text-decoration: none;
      }
    }
  }

  &:before {
    background: #fff;
    content: '';
    display: block;
    @extend .absoluteCenter;
    z-index: 1;
    opacity: 0;
    @include transition(all 0.25s ease-out );
  }

  &__logo{
    float: left;
    position: relative;
    z-index: 10;
    width: 189px;
    height: 40px;
    background: url(../img/lobster_Logo.svg);
    background-repeat: no-repeat;
    background-size: contain;

    @include breakpoint($tablet)  {
      width: 150px;
      height: 33px;
      // margin-top: 5px;
    }

    .sticky-top &,
    .innerPage &,
    &.small {
      background-image: url(../img/Lobster_Logo_dark.svg);
    }

    &.small {
      width: 118px;
      height: 25px;
      margin-bottom: 6rem;
    }
  }

  @include breakpoint($tablet)  {
    padding: 2.2rem 3rem 2.2rem 4.5rem;
  }

  @include breakpoint($mobile)  {
    display: flex;
    justify-content: space-between;
    padding: 2.2rem 3rem 2.2rem 4.5rem;
    padding: 2rem 5rem;
    font-size: $smaller-font-size-fixed;
    line-height: 40px;

    .topSection__logo {
      background: url(../img/icons/lobster_symbol.svg);
      background-position: center 0;
      background-repeat: no-repeat;
      width: 24px;
      height: 35px;
    }

    .forShow,.mHide {
      display: none !important;
    }

    .burgerMenu {
      display: block;
      width: 30%;
      text-align: left;
    }

    .mainMenu {
      width: 30%;
      a {
        font-size: $smaller-font-size-fixed;
      }
    }
  }

  .sticky-top &,
  .innerPage & {
    box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.2);
    background: #fff;

    .icon-navicon {
      color: #6f6f6f;
    }

    li.forShow {
      display: inline-block;
      opacity: 1;
    }

    a:not(.logo--small) {
      position: relative;
      z-index: 10;
    }

    a:not(.btn) {
      color: $top-menu-color;
    }

    &:before {
      opacity: 0.9;
    }
  }
}



.footer {
  background-color: #3D3D3D;
  padding: 7rem 5rem 2rem 5rem;
  color: #fff;
  @include font-size($smaller-font-size);

  .container {
    position: relative;
  }

  @include breakpoint($mobile) {
    text-align: center;
    padding: 45px 0;
  }

  &__line {
    display: none;
    background: #fff;
    height: 85px;
    @extend .flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.2);
    text-align: center;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 30;

    @include breakpoint($mobile) {
       .sticky-top &{
        display: flex;
      }
    }

    .oh & {
      display: none;
    }

    .btn {
      @extend .wide--mobile;
    }
  }


  &__menus {
    @extend .flex;
    justify-content: flex-end;
    align-items: flex-start;
    font-size: 13px;
    // font-weight: bold;

    @include breakpoint($mobile) {
      display: block;
      flex-direction: column;
      justify-content: center;
      padding-top: 50px;
      border-top: solid 1px rgba(255, 255, 255, 0.12);
      margin-top: 25px;
    }
  }

  &__menu {
    padding-left: 4.5rem;


    li {
      margin-bottom: 1rem;
    }

    @include breakpoint($mobile) {
      padding: 0;

      li {
        line-height: 1;
        margin: 0 0 25px 0;
      }
    }

    a {
      text-transform: uppercase;
      color: #fff;
    }
  }

  &__copy {
    margin-top: 5.5rem;
    width: 100%;
    text-align: right;
    color: $almost-white;
    font-size: 12px;

    @include breakpoint($mobile) {
      text-align: center;
      font-size: 10px;
      color: rgba(255, 255, 255, 0.5);

      a {
        &:before {
          content: '';
          display: block;
          clear: both;
        }
      }
    }

    a {
      color: #fff;
    }
  }

  &__social {
    @extend .flex;
    justify-content: flex-start;
    align-items: center;
    font-size: 0;
    float: left;
    color: $almost-white;

    @include breakpoint($mobile) {
      float: none;
      display: flex;
      text-align: center;
    }

    div {
      font-size: $default-font-size;
    }

    .socialMenu__item {
      background: $gray-icon;
      border: none;
      width: 24px;
      height: 24px;
      line-height: 24px;
      border-radius: 100%;

      i {
        font-size: 14px;
        color: #3D3D3D;
      }
    }
  }

  a:hover {
    text-decoration: none;
  }

  .poweredLink {
    display: block;
    background: url(../img/lobster-powered-logo.png) no-repeat;
    background-size: cover;
    width: 120px;
    height: 30px;
    margin: auto;

    @include breakpoint($not-mobile) {
      position: absolute;
      left: 0;
      bottom: 0;
    }

    @include breakpoint($mobile) {
      width: 100px;
      height: 25px;
      margin-top: 50px;
      margin-bottom: -10px;
    }
  }

}
