html {
	font-size: 62.5%;
}

body {
  font-family: $default-font;
	font-weight: normal;
	@include font-size($default-font-size);
  line-height: 1.43;
}

/*===============================
=            Headers            =
===============================*/
h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  line-height: 1.1;
	text-rendering: optimizeLegibility;
}

h1,.h1 {
  font-size: 36px;
	font-weight: bold;
}

h2,.h2 {
  font-size: 2.8rem;
}

.content .text-line {
	color: $header-gray-color;
}

h3,.h3 {
  font-size: 2.5rem;
}

h4 {
  font-size: 18px;
}

.h5, h5 {
	font-size: $default-font-size;
  font-weight: 600;
	margin-top: 1.5rem;
	margin-bottom: 1.5rem;
}

.editorContent {
  h3 {
    @extend .h5;
  }
	ul {
		margin: 1.5rem 2rem;
		list-style-type: disc;
		list-style-position: inside;
	}
}

.middleHeader {
	margin-top: 8rem;
	margin-bottom: 6rem;
	@extend .h1;
	font-weight: normal;
	text-align: center;

	&.gray {
		margin-top: 5rem;
		margin-bottom: 3rem;
		font-size: 2.8rem;
		color: $text-gray-color;
		@extend	.next-div-nopadding;
		clear: both;
	}

	& + .middleHeader {
		margin-top: -2rem;
	}
	// & + .coursesContainer {padding-top: 0;}
}

.separateLink {
	text-align: center;
	margin-top: 3.5rem;
	margin-bottom: -2rem;

	a {
		color:$brand-green;
		i {
			font-size: 2rem;
			color: $gray-text;
		}
	}
}


/*=====  End of Headers  ======*/
strong,
.bold {
  font-weight: bold;
}

p {
  margin-bottom: 1.5rem;
	word-wrap: break-word;
}


.special {
  font-style: italic;
}

.firm {
  font-variant: small-caps;
}

button, html, input, select, textarea {
    font-family: $default-font;
}
