.btn {
	display: inline-block;
	text-decoration: none !important;
	border: none;
	line-height: 40px;
	height: 42px;
	padding:0 1.2rem;
	text-transform: uppercase;
	border: 1px solid $pink;
	border-radius: 3px;
	transition: box-shadow .4s cubic-bezier(.25,.8,.25,1), background-color .4s cubic-bezier(.25,.8,.25,1);
	font-size: 14px;
	font-family: $default-font;
	background: $pink;
	color: #fff;
	cursor: pointer;
	@include transition(all 0.15s ease-out 0s);
	box-shadow: 0 2px 5px rgba(0, 0, 0, 0.29);
	text-align: center;
	@include appearance(none);

	> * {
		display: inline-block;
		vertical-align: middle;
	}

	&.sm {
		line-height: 3.8rem;
		height: 3.8rem;

		@include breakpoint($mobile) {
			line-height: 38px;
			height: 38px;
		}
	}

	&.static-sm {
		line-height: 38px;
		height: 38px;
		box-shadow: none;
	}

	@include breakpoint($mobile) {
		padding:0 10px;
  }

	&:hover {
		background-color: $pink-hover;
		border-color: $pink-hover;
		color: #fff;
	}

	&:not(.inactive):active {
		@include transform(scale(0.95));
	}

	&.bordered {
		@include bordered-btn($brand-green);
	}
	// button with transparent white bg
	&.white-border {
		border-color: #fff;
		color: #fff;
		background-color: rgba(255, 255, 255, 0.3);

		&:hover {
			background-color: rgba(255, 255, 255, 0.5);
		}
	}

	&.green-border {
		border-color: $brand-green;
		color: $brand-green;
		background-color: transparent;

		&:hover {
			background-color: $brand-green;
			color:#fff;
		}
	}

	&.wide--mobile {
		width: 75vw;
		max-width: 640px;
	}

	&.green {
		background-color: $green;
		border-color: $green;

		&:hover {
			background-color: lighten($green, 5%);;
	    border-color: lighten($green, 5%);;
		}
	}
}

.content .btn {
	max-width: 100%;
	text-overflow: ellipsis;
	overflow: hidden;
	white-space: nowrap;
}

&.minWidth {
	min-width: 16rem;
}

.big--btn {
	line-height: 48px;
	height: 50px;
	padding-left: 2rem;
	padding-right: 2rem;

	&:first-child {
		margin-bottom: 1rem;
	}
}

.loadMore {
	@extend .big--btn;
	@extend .minWidth;
	margin-top: 2rem;
}


